.services-page {
  &__categories {
    @media screen and (min-width: 900px) {
    }
  }
}

.service-category {

  &:not(:last-child) {
    margin-bottom: 30px;
    border-bottom: 1px solid rgba(0, 0, 0, .1);

    @media screen and (min-width: 900px) {
      margin-bottom: 65px;
      padding-bottom: 20px;
    }
  }

  &__title {
    margin-bottom: .6em;

    @media screen and (min-width: 900px) {
      margin-bottom: .8em;
    }
  }

  &__list {
    @include grid;
    @include grid-wrap;
  }

  &__item {
    @include grid-item(12);

    margin-bottom: 31px;
    text-decoration: none;

    @media screen and (min-width: 475px) {
      @include grid-item(6);
    }

    @media screen and (min-width: 900px) {
      @include grid-item(4);
      
      margin-bottom: 49px;
    }
  }
}

.service-preview {
  &__img-wrap {
    @include shadow;

    position: relative;
    margin-bottom: 12px;
    overflow: hidden;

    @media screen and (min-width: 900px) {
      margin-bottom: 18px;
    }

    &::before {
      content: 'Подробнее';
      position: absolute;
      z-index: 100;
      left: 50%;
      top: 50%;
      font-family: $ff--sec;
      font-weight: 300;
      font-size: 1.7rem;
      line-height: to-em(21px, 1.7rem);
      color: $txt-c--sec;
      transform: translate(-50%, -50%) scale(2);
      opacity: 0;
      transition: opacity .2s ease-out,
                  transform .2s ease-out;

    }
    &:hover::before {
      opacity: 1;
      transform: translate(-50%, -50%) scale(1);
    }

    &::after {
      content: '';
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      transform: scale(.2);
      opacity: 0;
      transition: background-color .2s ease-in-out,
                  transform .2s ease-in-out,
                  opacity .2s ease-in-out;
    }

    &:hover::after {
      opacity: 1;
      transform: scale(1);
      background-color: rgba(0, 173, 181, .8);
    }
  }

  &__img {
    display: block;
  }

  &__title {
    margin-bottom: 0.39em;

    @media screen and (min-width: 900px) {
      margin-bottom: .29em;
    }
  }

  &__desc {
    margin-bottom: .4em;
    font-family: $ff--sec;

    @media screen and (max-width: 599px) {
      font-size: 1.3rem;
      line-height: to-em(19px, 1.3rem);
    }

    @media screen and (min-width: 900px) {
      padding-right: 30px;
    }
  }
}