.article-preview-vrt {
  position: relative;
  padding: 0 10px;
  display: flex;
  flex-flow: column;
  height: 100%;
  font-family: $ff--sec;
  
  @media screen and (min-width: 600px) {
    padding: 0 15px;
  }
  &__tag {
    position: absolute;
    z-index: 100;
    top: 8px;
    left: 18px;

    @media screen and (min-width: 600px) {
      top: 20px;
      left: 35px;
    }
  }

  &__img {
    @include shadow;

    margin-bottom: 11px;
    display: block;
    max-width: 100%;
    height: 273px;
    object-fit: cover;
    
    @media screen and (min-width: 600px) {
      height: 360px;
      margin-bottom: 13px;
    }
  }

  &__date {
    margin-bottom: to-em(5px, 1rem);
    font-size: 1rem;
    line-height: to-em(11px, 1rem);
    color: rgba(0, 0, 0, .5);
    
    @media screen and (min-width: 600px) {
      font-size: 1.2rem;
      line-height: to-em(22px, 1.2rem);

    }

  }

  &__desc {

    font-size: 1.3rem;
    line-height: to-em(19px, 1.3rem);

    @media screen and (min-width: 600px) {
      font-size: 1.5rem;
      line-height: to-em(23px, 1.5rem);
    }

  }

  &__link {
    margin-top: auto;

    &::before {
      content: '';
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
    }
    @media screen and (max-width: 599px) {
      @include vis-hid;
    }
  }
}

.article-preview-hrz {
  display: none;
  min-height: 150px;
  transition: 
              border-right-color .2s ease-out;

  @media screen and (min-width: 600px) {
    padding-right: 15px;
    border-right: 3px solid transparent;
  }

  @media screen and (min-width: 900px) {
    padding-right: 30px;
    min-height: 267px;
  }
  &.is-visible {
    display: flex;
  }
  &.is-active,
  &:hover {
    @media screen and (min-width: 600px) {
      border-right-color: $txt-c--colored-prim;
    }
  }

  &__inner {
    position: relative;
    padding: 20px 0;
    display: flex;
    border-bottom: 1px solid rgba(0, 0, 0, .1);
  }

  &__txt {
    display: flex;
    flex-wrap: wrap;
    flex-grow: 1;

    @media screen and (min-width: 900px) {
      margin-right: 28px;
      padding-top: 20px;
      padding-bottom: 30px;
    }
  }

  &__title {
    padding-right: 10px;
    flex: 0 0 100%;    
    transition: color .2s ease-out;

    @media screen and (max-width: 899px) {
      max-height: 76px;
      font-size: 1.3rem;
      line-height: to-em(19px, 1.3rem);
      overflow: hidden;
    }

    @media screen and (min-width: 900px) {
      line-height: to-em(25px, 2.2rem);
    }
    .article-preview-hrz.is-current &,
    .article-preview-hrz:hover & {
      color: $txt-c--colored-prim;
    }
  }

  &__date {
    margin-top: auto;
    font-size: 1rem;
    line-height: 1em;
    font-family: $ff--sec;
    color: $c--grey;

    @media screen and (max-width: 899px) {
      position: absolute;
      right: 0;
      bottom: 20px;
    }

    @media screen and (min-width: 900px) {
      font-size: 1.2rem;
      margin-left: auto;
    }
  }
  &__tag {
    margin-top: auto;

    @media screen and (min-width: 900px) {
    }

  }
  &__img {
    flex: 0 0 55px;
    display: flex;
    align-self: flex-start;

    @media screen and (min-width: 900px) {
      flex: 0 0 170px;
    }

    img {
      object-fit: cover;
    }
  }

}
