.styled-list {
  list-style-type: none;
  padding-left: 0;

  & li {
    position: relative;
    padding-left: 20px;

    &::before {
      content: '';
      position: absolute;
      left: 0;
      top: calc(50% - 3px);
      display: inline-block;
      width: 7px;
      height: 7px;
      border-radius: 50%;
      border: 2px solid $c--prim;
    }
  }
}

