.projects-page {
  &__title {
    padding-bottom: to-em(28px, 5.4rem);

  }
  &__filter {
    margin-bottom: 30px;
    
    @media screen and (min-width: 900px) {
      margin-bottom: 40px;
    }
  }
  &__gallery {

    @media screen and (min-width: 900px) {
      margin-bottom: 77px;
    }
  }

  &__feedback {
    @media screen and (min-width: 900px) {
      margin-bottom: 120px;
    }
  }
}
.projects-filter {

  &__btn {

    &:not(:last-child) {
      margin-right: 7px;
      margin-bottom: 7px;

      @media screen and (min-width: 900px) {
        margin-bottom: 0;
      }
    }
  }
}
.projects-gallery {

  &__item {
    display: block;
    text-decoration: none;
  }

  &__img {
    @include shadow;
  }
}