.card-page {

  &__title {
    margin-bottom: .8em;
    padding-top: 1.3em;
    font-size: 2.4rem;
    line-height: to-em(26px, 2.4rem);

    @media screen and (max-width: 899px) {
      padding-right: 50px;
    }
    @media screen and (min-width: 900px) {
      font-size: 3.2rem;
      line-height: to-em(30px, 3.2rem);
    }
  }

  &__projects {
    display: none;

    @media screen and (min-width: 900px) {
      display: block;
      padding-bottom: 21px;
      margin-bottom: 50px;
    }
  }

  &__breadcrumbs {
    @media screen and (min-width: 900px) {
      margin-bottom: 41px;
    }
  }
}

.card  {
  @include grid;

  &__content {
    @include grid-item(12);
    
    @media screen and (min-width: 900px) {
      @include grid-item(8);
    }

    @media screen and (min-width: 1000px) {
      @include grid-item(9);
    }
  }

  &__sidebar {

    @media screen and (min-width: 900px) {
      @include grid-item(4);
    }

    @media screen and (min-width: 1000px) {
      @include grid-item(3);
    }
  }

  &__content-header {
    margin-bottom: 35px;
  }

  &__desc {
    margin-bottom: 24px;
    font-family: $ff--sec;

    @media screen and (max-width: 599px) {
      font-size: 1.3rem;
      line-height: to-em(19px, 1.3rem);
    }

    @media screen and (min-width: 600px) {
      margin-bottom: 0;
      padding-right: 150px;
    }
  }

  &__gallery {
    position: relative;
    margin-bottom: 39px;

    @media screen and (min-width: 600px) {
      margin-bottom: 61px;
    }
  }

  &__gallery-slider {

    @media screen and (min-width: 900px) {
      margin-bottom: 12px;
    }
  }

  &__gallery-desc {
    display: none;

    @media screen and (min-width: 900px) {
      display: block;
      margin-bottom: 0;
      font-family: $ff--sec;
    }
  }

  &__gallery-label {
    position: absolute;
    top: 28px;
    left: 30px;
    z-index: 1;
    max-width: 160px;
    font-size: 1.5rem;
    line-height: to-em(22px, 1.5rem);
    font-family: $ff--sec;
    color: $txt-c--sec;

    @media screen and (min-width: 900px) {
      font-size: 1.8rem;
      line-height: to-em(22px, 1.8rem);
    }
  }

  &__calc {
    margin-bottom: 40px;

    @media screen and (min-width: 900px) {
      margin-bottom: 70px;
    }
  }

  &__accordion {
    margin-bottom: 40px;

    @media screen and (min-width: 900px) {
      margin-bottom: 102px;
    }
  }

  &__advantage {
    margin-bottom: 40px;
  }
  &__board {
    margin-bottom: 68px;
  }

  &__board-two {
    margin-bottom: 58px;
  }
  &__advantage-two {
    margin-bottom: 65px;
  }
}

.card-design {
  
  &__accordion {
    margin-bottom: 79px;
  }


}

.nav-sidebar {
  display: none;

  @media screen and (min-width: 900px) {
    display: block;
  }

  &__list {
    @extend %list-reset;
    
    @media screen and (min-width: 900px) {
      margin-bottom: 30px;
      border: 1px solid $c--grey-light;
    }
  }
  &__item {
    &:not(:last-child) {

      @media screen and (min-width: 900px) {
        border-bottom: 1px solid $c--grey-light;
      }
    }
  }

  &__link {

    @media screen and (min-width: 900px) {
      @include txt-nowrap;

      margin: -1px;
      padding: 1.58em 1.27em;
      display: block;
      border-left: 2px solid transparent;
      font-family: $ff--sec;
      text-decoration: none;
      transition: color .2s ease-in-out,
                  background-color .2s ease-in-out,
                  border-color .2s ease-in-out;

    }

    &:hover,
    &:focus, 
    &.is-active {
      border-color: $c--prim;
      color: $txt-c--colored-prim;
      background-color: $c--prim-light;
      outline: none;
    }
  }

  &__btn {

    @media screen and (min-width: 900px) {
      margin-bottom: 30px;
      max-width: 100%;
    }

  }
}
