.slider {
  position: relative;

  &__img {
    display: block;
    width: 100%;
    height: auto;
  }

  &__pagination {
    position: absolute;
    left: 50%;
    bottom: 0;
    min-height: 30px;
    font-size:  1.3rem;
    line-height: 30px;
    font-family: $ff--sec;
    color: $txt-c--colored-prim;
    transform: translateX(-50%);

    @media screen and (min-width: 600px) {
      display: none;
    }
  }

  &__btn {
    position: absolute;
    top: 50%;
    z-index: 1;
    padding: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 25px;
    height: 25px;
    border: 2px solid transparent;
    border-radius: 50%;
    background-color: transparent;
    cursor: pointer;
    transform: translateY(-50%);
    transition: border-color .2s ease-in-out;

    @media screen and (min-width: 600px) {
      width: 30px;
      height: 30px;
    }

    &:focus,
    &:hover {
      border-color: $c--prim;
      stroke: $c--prim;
      outline: none;
    }

    &--fill-round {
      background-color: rgba(0, 0, 0, .6);
      font-size: 1.2rem;
      stroke: $c--white;

      @media screen and (min-width: 600px) {
        font-size: 1.4rem;
      }
    }
  }

  &__btn--prev {
    left: 8px;

    @media screen and (min-width: 600px) {
      left: 20px;

    }
  }

  &__btn--next {
    right: 10px;
    
    @media screen and (min-width: 600px) {
      right: 20px;
    }
  }
}

.gallery-slider {
  margin-bottom: 13px;
}

.our-team-slider {
  
  @media screen and (max-width: 599px) {
    margin: 0 -20px;
    padding-bottom: 53px;
    overflow: hidden;
    
  }
  
  & .slider__container {
    max-width: 950px;
    margin: 0 -60px;
    
    @media screen and (min-width: 600px) {
      margin: 0 auto;
    }
  }

  & .slider__slide {
    display: flex;
    justify-content: center;
  }

  & .slider__btn {
    top: auto;
    bottom: 0;
    stroke: $c--prim;
    
    @media screen and (max-width: 599px) {
      transform: translateY(0);
    }

    @media screen and (min-width: 600px) {
      top: 29%;
      bottom: auto;
    }

    @media screen and (max-width: 599px) {
      font-size: 1.2rem;
    }

    &--prev {
      left: -10px;
      
      @media screen and (max-width: 599px) {
        left: 10px;
      }
    }

    &--next {
      right: -10px;

      @media screen and (max-width: 599px) {
        right: 10px;
      }
    }
  }

}

.blog-preview-slider {
  margin: 0 -10px 20px;
  display: flex;

  @media screen and (min-width: 600px) {
    margin: 0 -15px;
  }
  
  & .swiper-slide {
    max-width: 225px;//205width of article-preview + 10px padding * 2

    @media screen and (min-width: 600px) {
      max-width: 300px;//270width of article-preview + 15px padding * 2 
    }

    &:not(:last-child) {
      @media screen and (min-width: 600px) {
        border-right: 1px solid rgba(0, 0, 0, .1);
      }
    }
  }
}

.card-slider,
.article-slider {

  @media screen and (max-width: 599px) {
    margin: 0 -20px 15px;
    padding-bottom: 37px;
  }

  & .slider__btn {

    @media screen and (max-width: 599px) {
      top: auto;
      bottom: 0;
      width: 30px;
      height: 30px;
      font-size:  1.2rem;
      stroke: $c--prim;
      background-color: transparent;
      transform: translateY(0);
    }
  }
}

.article-slider {

  & .swiper-slide {
    height: 100%;
  }
}

.filter__slider {
  & .swiper-slide {
    width: auto;
  }
}