.link {
  @include txt-nowrap;

  display: block;
  font-family: $ff--sec;
  text-decoration: none;
  color: $txt-c--colored-prim;
  transition: color .3s ease;

  @media screen and (max-width: 899px) {
    font-size: 1.3rem;
    line-height: to-em(18px, 1.3rem);
  }

  @media screen and (min-width: 475px) {
    max-width: 200px;
  }

  @media screen and (min-width: 600px) {
    max-width: 250px;
  }

  &--framed {
    @media screen and (max-width: 599px) {
      padding: .5em 1em;
      width: 100%;
      min-height: 50px;
      border: 1px solid $c--prim;
      line-height: calc(50px - .5em * 2);
      text-align: center;
      transition: color .3s ease,
        background-color .3s ease;

    }
    &:active {
      @media screen and (max-width: 599px) {
        background-color: $c--prim;
        color: $txt-c--sec;
      }
    }

  }

  &:hover {

    @media screen and (min-width: 600px) {
        color: $txt-c--colored-sec;
    }
  }
}