.feedback {
  position: relative;
  
  &--narrow {
    @media screen and (min-width: 600px) {
      max-width: 368px;
    }
  }
  &--fill {
    margin: 0 -20px;
    padding: 30px 0 40px;
    background-color: $c--prim;
    color: $txt-c--sec;

    @media screen and (min-width: 600px) {
      background: url('../img/design/feedback_bg.jpg') no-repeat 100% 0 / contain $c--prim;
      background-blend-mode: screen;
    }

    @media screen and (min-width: 900px) {
      margin: 0;
      padding: 77px 0 90px 80px;
    }

    &::before, &::after {
      @media screen and (min-width: 1100px) {
        content: '';
        position: absolute;
        right: 130px;
        display: block;
      }
    }
    &::before {
      @media screen and (min-width: 1100px) {
        top: -30px;
        width: 250px;
        height: 355px;
        background: url('../img/design/mob-phones.png') no-repeat 0 0 / cover;
      }
    }
    &::after {
      @media screen and (min-width: 1100px) {
        bottom: 20px;
        width: 250px;
        height: 25px;
        border-radius: 50%;
        background-color: rgba(0, 0, 0, 0.2);
        filter: blur(10px);
      }
    }
  }

  &__inner {
    padding-left: 20px;
    padding-right: 20px;
    
    .feedback--fill & {
      max-width: 608px;
    }
  }

  &__title {
    
    .feedback--narrow & {
        margin-bottom: .5em;

      @media screen and (min-width: 600px) {
        margin-bottom: 1.1em;
      }
    }

    .feedback--fill & {
      margin-bottom: 0.65em;
      padding-right: 40px;
      font-size: 2.4rem;
      line-height: to-em(24px, 2.4rem);

      @media screen and (min-width: 600px) {
        font-size: 3.2rem;;
        line-height: to-em(36px, 3.2rem);
      }

    }

  }

}

.form {
  display: flex;
  flex-flow: column;

  &--wide {
    @media screen and (min-width: 475px) {
      flex-flow: row wrap;
    }
  }

  &__input, &__select {
    margin-bottom: 20px;

    .form--wide & {

      @media screen and (min-width: 900px) {
        margin-bottom: 30px;
      }
    }
  }

  &__input {

    .form--wide & {
      @media screen and (min-width: 475px) {
        flex: 1 1 50%;
      }
      
      &:first-child {
        @media screen and (min-width: 475px) {
          border-right-color: transparent;
        }
      } 
    }
  }

  &__btn {
    margin-bottom: 18px;
    
    @media screen and (min-width: 475px) {
      margin-right: 20px;
      margin-bottom: 0;
    }
    @media screen and (min-width: 600px) {
      margin-bottom: 18px;
    }
    
    .form--wide & {
      margin-bottom: 10px;

      @media screen and (min-width: 475px) {
        margin-bottom: 0;
      }
    }
  }

  &__footer {
    display: flex;
    flex-flow: column;

    @media screen and (min-width: 475px) {
      flex-flow: row;
      align-items: center;
    }
    .feedback--narrow & {

      @media screen and (min-width: 600px) {
        flex-flow: column;
        align-items: start;
      }
    }
  }

  &__policy {
    margin-bottom: 0;
    font-family: $ff--sec;
    font-size: .9rem;
    line-height: to-em(13px, .9rem);
    opacity: .5;

    @media screen and (min-width: 900px) {
      font-size: 1rem;
      line-height: to-em(15px, 1rem);
    }

    .form--wide & {
      @media screen and (min-width: 900px) {
        padding-right: 20px;
     }
    }
  }
}
