.menu {
  display: flex;
  font-family: $ff--sec;

  .drop-content__inner & {
    margin-bottom: 20px;
  }
    // так як елемент пригаючий display: none;
    //не дае йому шириини і висоти при побудові сторінки
    .header__top-row & {
      @media screen and (max-width: 1099px) {
        display: none;
      }
    }

    &__list {
      @extend %list-reset;

      display: flex;
      flex-flow: column nowrap;
    }

    &__link {
      display: block;
      text-decoration: none;
    }
  }

  .header-menu {
   
    &__list {
      @media screen and (min-width: 1100px) {
        flex-flow: row nowrap;
        align-items: center;
      }
    }

    &__item {
      &:not(:last-child) {
        @media screen and (min-width: 1100px) {
          margin-right: 25px;
        }
      }
    }

    &__link {
      position: relative;
      font-size: 1.3rem;
      line-height: to-em(15px, 1.3rem);
      color: $txt-c--colored-prim;

      @media screen and (max-width: 1099px) {
        padding: to-em(10px 0px, 1.3rem);
        color: $txt-c--colored-prim;
      }
      @media screen and (min-width: 900px) {
        padding: to-em(5px 0px, 1.3rem);
      }
      @media screen and (min-width: 1100px) {
        color: $txt-c--sec;
      }

      &::after {

        @media screen and (min-width: 1100px) {
          content: '';
          position: absolute;
          left: 50%;
          bottom: 0;
          display: block;
          width: 0;
          height: 1px;
          background-color: $c--white;
          transition: left .2s ease-out,
            width .2s ease-out;
        }
      }

      &--active::after,
      &:hover::after {
        @media screen and (min-width: 1100px) {
          left: 0;
          width: 100%;
        }
      }
    }
  }

