.our-advantage {
  padding-top: 20px;
  padding-bottom: 20px;
  
  &:not(:last-child) {

    @media screen and (min-width: 900px) {
      margin-bottom: 35px;
    }
  }

  &--hidden {
    @media screen and (max-width: 599px) {
      @include vis-hid;
    }
  }

  &.line {
    @media screen and (min-width: 600px) {
      padding-top: 45px;
      border-top: 1px solid rgba(0, 0, 0, .1);
    }
  }

  &__link-more {
    @media screen and (max-width: 599px) {
      margin-bottom: 35px;
    }

    @media screen and (min-width: 600px) {
      @include vis-hid;
    }
  }

  &__title {
    margin-bottom: to-em(15px, 1.5rem);
    font-family: $ff--sec;
    font-size: 1.5rem;
    line-height: to-em(16px, 1.5rem);
    color: $txt-c--colored-sec;


    @media screen and (min-width: 600px) {
      margin-bottom: to-em(20px, 1.8rem);
      font-size: 1.8rem;
      line-height: 1em;
    }
  }


  &__list {
    @extend %list-reset;

    @media screen and (min-width: 600px) {
      @include grid;
      @include grid-wrap;
    }
  }

  &__item {
    display: flex;
    flex-flow: column;
    font-size: 1.8rem;
    line-height: to-em(20px, 1.8rem);

    @media screen and (max-width: 599px) {
      padding-left: 10px;
      border-left: 1px solid $c--sec;

      &:not(:last-child) {
        margin-bottom: 16px;
      }
    }
    
    @media screen and (min-width: 600px) {
      @include grid-item(6);

      margin-bottom: 20px;
      font-size: 2.2rem;
      line-height: to-em(25px, 2.2rem);
    }

    @media screen and (min-width: 900px) {
      @include grid-item(3);

      margin-bottom: 0;
    }
  }

  &__num {
    font-size: 2.2rem;
    line-height: 1em;

    @media screen and (min-width: 600px) {
      font-size: 3.2rem;
      line-height: 1em;
    }

    &--blue {
      color: $txt-c--colored-prim;
    }
  }
}

.service-advantage {

  &__title {
    margin-bottom: to-em(26px, 1.3rem);
    padding-top: to-em(11px, 1.3rem);
    font-family: $ff--sec;
    font-size: 1.3rem;
    line-height: to-em(19px, 1.3rem);
    color: $txt-c--colored-sec;

    @media screen and (min-width: 600px) {
      font-size: 1.5rem;
      line-height: to-em(19px, 1.5rem);
    }
  }
  
  &__list {
    @extend %list-reset;
    @include grid;
    @include grid-wrap;

  }
  
  &__item {
    @include grid-item(12);

    display: flex;
    flex-flow: column;
    font-size: 2rem;
    line-height: 1em;
    
    @media screen and (min-width: 600px) {
      @include grid-item(6);
      
      font-size: 2.2rem;
    }

    @media screen and (min-width: 900px) {
      @include grid-item(4);

      margin-bottom: 12px;
    }
  }

  &__item-desc {
    font-family: $ff--sec;
    font-size: 1.3rem;
    line-height: to-em(19px, 1.3rem);

    @media screen and (min-width: 600px) {
      font-size: 1.5rem;
      line-height: to-em(22px, 1.5rem);
    }
  }

  &__item-heading {
    margin-bottom: to-em(7px, 2rem);
    font-family: $ff--prim;
    font-weight: 400;
  }
}

