.tag {
  @include txt-nowrap;

  padding: to-em(7px 13.5px, .9rem);
  min-width: 50px;
  max-width: 200px;
  font-size: .9rem;
  line-height: to-em(12px, .9rem);
  font-family: $ff--sec;
  letter-spacing: .04em;
  text-align: center;

  @media screen and (min-width: 600px) {
    padding: to-em(8px 13px, 1.2rem);
    font-size: 1.2rem;
    line-height: 1em;

  }
  &--blue {
    color: $txt-c--colored-prim;
    border: 1px solid;
    border-radius: to-em(20px, .9rem);
    background-color: transparent;
    cursor: pointer;
    transition: background-color .2s ease-out;

    &.is-active,
    &:hover {
      background-color: $c--prim-light;
    }
  }
  
  &--float {
    border-radius: to-em(20px, .9rem);
    background-color: rgba(0, 0, 0, .6);
    border: 1px solid rgba(0, 0, 0, .1);
    color: $txt-c--sec;
    
  }  
}