.icon {
  display: inline-block;
  width: 1em;
  height: 1em;
  stroke-width: 0;
  stroke: currentColor; 
  fill: currentColor;
  transition: fill .3s ease;
}

.icon-social {
  fill: $c--white;
}

.icon-telegram {
  margin-left: -.2em;
}
.icon-play-btn {
  margin-right: -2px;
  fill: $c--white;
}

.icon-arrow {
  stroke-width: 2px;
  stroke: inherit;
  transition: stroke .3s ease;
}


