.select {
  padding: 0 20px;
  display: flex;
  min-height: 50px;
  min-width: 150px;
  border: none;
  border-left: 1px solid $c--prim;
  border-right: 1px solid $c--prim;
  font-family: $ff--sec;
  background-image: url('data:image/png;base64,'), linear-gradient( to bottom, $c--white, $c--white);
  background-repeat: no-repeat, repeat;
  background-position: right 14px top 50%, 0 0;
  background-size: 30px 30px, 100%;
  appearance: none;
  color: $txt-c--colored-prim;
  cursor: pointer;

  @media screen and (min-width: 600px) {
    background-position: right 20px top 50%, 0 0;
  }
  @media screen and (max-width: 899px) {
    font-size: 1.3rem;
    line-height: to-em(18px, 1.3rem);
  }

  &:focus {
    outline: none;
    background-image: url('data:image/png;base64,'), linear-gradient(to bottom, $c--prim-light, $c--prim-light);
  }
}
