.project-page {

  &__breadcrumbs {
    @extend .card-page__breadcrumbs;
  }

  &__title-wrap {
    display: flex;
    align-items: baseline;
    justify-content: space-between;
  }

  &__title {
    @extend .card-page__title;
    margin-bottom: 0.5em;
    padding-right: 0;
  }

  &__link {
    display: none;
    text-decoration: none;
    
    @media screen and (min-width: 1200px) {
      display: block;
    }
  }

  &__project-manager {
    margin-bottom: 48px;

    @media screen and (min-width: 900px) {
      margin-bottom: 74px;
    }
  }

  &__projects {
    padding-bottom: 38px;
    @media screen and (min-width: 900px) {
      padding-bottom: 30px;
    }
  }
}

.project-info {
  margin-bottom: 53px;

  @media screen and (min-width: 900px) {
    margin-bottom: 68px;
  }

  &__services {
    @include grid;
    @include grid-wrap;

    margin-bottom: 44px;

    @media screen and (min-width: 900px) {
      margin-bottom: 80px;
    }
  }

  &__services-caption {
    @include grid-item(12);

    margin-bottom: to-em(8px, 1rem);
    font-family: $ff--sec;
    font-size: 1rem;
    line-height: to-em(22px, 1rem);
    color: lighten($c--grey, 20%);
    
    @media screen and (min-width: 900px) {
      font-size: 1.2rem;
    }
  }

  &__services-sidebar {
    @include grid-item(12);

    display: flex;
    flex-flow: column;
    align-self: stretch;

    @media screen and (max-width: 599px) {
      margin-bottom: 26px;
    }

    @media screen and (min-width: 600px) {
      @include grid-item(5);
    }

    @media screen and (min-width: 900px) {
      @include grid-item(4);
    }
  }

  &__services-img {
    @include grid-item(12);

    @media screen and (min-width: 600px) {
      @include grid-item(7);
    }
    @media screen and (min-width: 900px) {
      @include grid-item(8);
    }

    & img {
      display: flex;
      object-fit: cover;
    }
  }

  &__services-list {
    @extend %list-reset;
    margin-bottom: 23px;
    
    border-bottom: 1px solid rgba(0, 0, 0, .1);
    font-family: $ff--sec;    
    
    @media screen and (min-width: 900px) {
      margin-bottom: auto;
    }
  }
  &__services-item {
    border-top: 1px solid rgba(0, 0, 0, .1);
  }

  &__services-link {
    padding: to-em(18px 0px 17px, 1.3rem);
    display: block;
    font-size: 1.3rem;
    line-height: to-em(22px, 1.3rem);
    text-decoration: none;
    transition: color .2s ease;

    @media screen and (min-width: 900px) {
      font-size: inherit;
      padding-bottom: to-em(19px, 1.5rem);
    }
    &:hover {
      color: lighten($c--grey, 20%);
    }
  }

  &__data {
    display: flex;
    flex-wrap: wrap;
    font-size: 1.3rem;
    line-height: to-em(19px, 1.3rem);
    font-family: $ff--sec;
  
    @media screen and (min-width: 900px) {
      font-size: inherit;
    }
  }

  &__data-name,
  &__data-desc {
    flex: 0 1 50%;
    
    &:not(:nth-last-child(-n+2)) {
      margin-bottom: 7px;

      @media screen and (min-width: 900px) {
        margin-bottom: 11px;
      }
    }
  }

  &__data-name {
    color: lighten($c--grey, 20%);
  }
  &__data-desc {
    padding-left: 10px;

    @media screen and (min-width: 900px) {
      padding-left: 15px;
    }
  }

  &__gallery {
    display: flex;
    flex-wrap: wrap;
    margin-left: -10px;
    margin-right: -10px;

    @media screen and (min-width: 900px) {
      margin: 0 -15px;
    }
  }

  &__gallery-item {
    padding-left: 10px;
    padding-right: 10px;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;

    @media screen and (max-width: 599px) {
      flex-direction: column;
      flex-direction: column-reverse;
    }

    @media screen and (min-width: 900px) {
      padding-left: 15px;
      padding-right: 15px;
    }

    &:not(:last-child) {
      margin-bottom: 20px;

      @media screen and (min-width: 900px) {
        margin-bottom: 30px;
      }
    }
    img {
      display: block;
      object-fit: cover;
    }
  }

  &__gallery-item--narrow {
    flex: 0 1 50%;
  }

  &__desc {
    margin-bottom: 32px;
    font-family: $ff--sec;
    font-size: 1.3rem;
    line-height: to-em(19px, 1.3rem);

    @media screen and (min-width: 600px) {
      padding: 20px 0 10px;
      margin-bottom: 0;
    }
    @media screen and (min-width: 900px) {
      margin-bottom: 37px;
      padding: 73px 5px 0;
      flex: 0 0 percentage(8/12);
      font-size: inherit;
      line-height: to-em(22px, 1.5rem);
    }
  }

  &__desc-title {
    margin-bottom: to-em(8px, 1.3rem);
    color: $txt-c--colored-sec;

    @media screen and (min-width: 900px) {
      margin-bottom: to-em(11px, 1.5rem)
    }

  }
  &__desc-txt {
    margin-bottom: 0;
  }

}

.project-manager {
  @include grid;

  @media screen and (max-width: 599px) {
    @include grid-wrap;
  }
    
  &__manager{
    @include grid-item(12);
    
    margin-bottom: 35px;
    display: flex;
    justify-content: center;

    @media screen and (min-width: 600px) {
      @include grid-item(6);

      margin-bottom: 0;
    }

    @media screen and (min-width: 900px) {
      justify-content: start;
      padding-left: 37px;
    }
  }

  &__feedback {
    @include grid-item(12);
    @media screen and (min-width: 600px) {
      @include grid-item(6)
    }
  }
}