.board {
  padding: 50px 30px;
  background-color: $c--grey-light;
  text-align: center;

  &__title {

  }

  &__list {
    @extend %list-reset;

    display: flex;
    flex-flow: row wrap;
    font-family: $ff--sec;
  }

  &__item {
    flex: 0 1 33.333%;
    margin-bottom: 25px;
    padding-top: 60px;
    background-repeat: no-repeat;
    background-position: 50% 21%;
    background-size: 40px 40px;
  }

  &__footer {
    padding-top: 51px;
    border-top: 1px solid $c--sec;
  }

  &__footer-txt {
    margin-bottom: 0.6em;
    font-family: $ff--sec;
  }

  &__footer-price {
    margin-bottom: 0;
    font-size: 3.2rem;
    line-height: to-em(30px, 3.2rem);

    del {
      margin-right: 9px;
      font-size: 2.2rem;
      line-height: to-em(30px, 2.2rem);
      color: $c--grey;
    } 
  }
}
.board-one {

  &__list {
    margin-bottom: 30px;
  }

  &__item {
    &:nth-child(1) {
      background-image: url('../img/design/sprite.svg#pencil');
    }
    &:nth-child(2) {
      background-image: url('../img/design/sprite.svg#plan');
    }
    &:nth-child(3) {
      background-image: url('../img/design/sprite.svg#3d-visual');
    }
    &:nth-child(4) {
      background-image: url('../img/design/sprite.svg#advantage-ic3');
      background-size: 30px 40px;

    }
    &:nth-child(5) {
      background-image: url('../img/design/sprite.svg#supervision');
    }
    &:nth-child(6) {
      background-image: url('../img/design/sprite.svg#supervision');
    }
  }
}
.board-two {

  &__item {
    &:nth-child(1) {
      background-image: url('../img/design/sprite.svg#advantage-ic1');
      background-size: 30px 40px;
    }
    &:nth-child(2) {
      background-image: url('../img/design/sprite.svg#advantage-ic2');
    }
    &:nth-child(3) {
      background-image: url('../img/design/sprite.svg#advantage-ic3');
      background-size: 30px 40px;
    }
    &:nth-child(4) {
      background-image: url('../img/design/sprite.svg#advantage-ic4');
    }
    &:nth-child(5) {
      background-image: url('../img/design/sprite.svg#advantage-ic5');
    }
    &:nth-child(6) {
      background-image: url('../img/design/sprite.svg#advantage-ic6');
    }
  }
}