.footer {
  padding-top: 40px;
  padding-bottom: 10px;
  font-size: 1.3rem;
  line-height: to-em(19px, 1.3rem);
  font-family: $ff--sec;
  background-color: $c--grey-light;

  @media screen and (min-width: 600px) {
    font-size: 1.4rem;
    line-height: to-em(22px, 1.4rem);
  }


  @media screen and (min-width: 900px) {
    padding-top: 62px;
  }

  &__inner {
    @include grid;

    margin-bottom: 40px;

    @media screen and (min-width: 900px) {
      margin-bottom: 58px;
    }
  }
  
  &__logo {
    margin-bottom: 25px;
    
    @media screen and (min-width: 900px) {
      margin-bottom: 33px;
    }
  }
  &__contacts {
    @include grid-item(12);

    @media screen and (min-width: 600px) {
      @include grid-item(6);
    }

    @media screen and (min-width: 900px) {
      @include grid-item(4);
    }
  }
  &__menu {
    @include grid-item(2);

    @media screen and (max-width: 599px) {
      @include vis-hid;
    }
  }
  &__list {
    @extend %list-reset;
    @include grid-item(3);

    min-width: 0;

    @media screen and (max-width: 899px) {
      @include vis-hid;
    }
  }
  &__list-item {
    margin-bottom: to-em(12px, 1.4rem);
  }

  &__link {
    @include txt-nowrap;

    color: $txt-c--colored-prim;
    transition: color .2s ease;
    text-decoration: none;

    &:hover {
      color: $txt-c--colored-sec;
    }
  }
  &__copy-wrap {
    border-top: 1px solid rgba(0, 0, 0, .1);
  }
  &__copy {
    @include grid;

    padding-top: 2em;
    padding-bottom: 1em;
    flex-flow: column;
    justify-content: center;
    font-size: 0.9rem;
    line-height: to-em(13px, .9rem);
    color: rgba(0, 0, 0, .3);

    @media screen and (max-width: 599px) {
    }

    @media screen and (min-width: 600px) {
      flex-flow: row;
      align-items: baseline;
      font-size: 1.2rem;
      line-height: to-em(16px, 1.2rem);
    }
  }
  &__copy-txt {
    @include grid-item(12);

    margin-bottom: to-em(15px, .9rem);

    @media screen and (min-width: 600px) {
      @include grid-item(6);

      margin-bottom: 0;
    }
  }

  &__copy-link {
    @include grid-item(12);

    text-decoration: none;
    transition: color .2s ease;
    
    @media screen and (min-width: 600px) {
      @include grid-item(6);
    }

    &:hover {
      color: $txt-c--colored-prim;
    }
  }
  .contacts__address {
    margin-bottom: 25px;

    @media screen and (min-width: 900px) {
      margin-bottom: 46px;
    }
  }
  .contacts__address-link {
    @media screen and (min-width: 900px) {
      margin-bottom: 20px;
    }
  }
}