.breadcrumbs {
  padding-top: 9px;
  padding-bottom: 9px;
  background-color: $c--grey-light;
  
  @media screen and (max-width: 899px) {
    @include vis-hid;
  }

  &__list {
    @extend %list-reset;

    display: flex;
    align-items: center;
  }

  &__item {
    display: flex;
    align-items: center;
    font-family: $ff--sec;
    color: $c--grey;
    
    &:not(:last-child) {
      margin-right: 9px;
    }
  }

  &__link {
    font-size: 1.1rem;
    line-height: to-em(14px, 1.1rem);
    text-decoration: none;
  }

  &__item-separator {
    font-size: .9em;
    margin-right: 10px;
  }
}

