$container-width: 1170px;
$container-gutter-width: 40px;
$item-gutter-width: 30px;

.container {
  margin: 0 auto;
  padding-left: $container-gutter-width / 2;
  padding-right: $container-gutter-width / 2;
  // .container width ...px + padding 20px *2
  max-width: $container-width + $container-gutter-width;
}

@mixin grid() {
  margin-left: -$item-gutter-width / 2;
  margin-right: -$item-gutter-width / 2;
  display: flex;
}
@mixin grid-wrap() {
  flex-flow: row wrap;
}

@mixin grid-item($columns, $parentColumns: 12) {
  padding-left: $item-gutter-width/2;
  padding-right: $item-gutter-width/2;
  flex: 0 1 percentage(($columns / $parentColumns));
  max-width: percentage(($columns / $parentColumns));
}