.header {
  // position: fixed;
  position: relative;
  // top: 0;
  // z-index: 100;
  width: 100%;
  background-color: $c--white;
  box-shadow: 0 7px 16px rgba(0, 0, 0, .2);
  transition: top .3s ease;

  &__top-row {
    color: $txt-c--sec;
    background-color: $c--prim;
    
    @media screen and (min-width: 1100px) {
      padding-top: 7px;
      padding-bottom: 7px;
    }
  }
  &__bottom-row {
    min-height: 50px;

    .main-page & {
      position: relative;

      &::after {
        content: '';
        position: absolute;
        right: 0;
        top: 100%;
        display: block;
        width: 50px;
        height: 150px;
        background: url('../img/design/sprite.svg#flag') no-repeat 0 0 / 100% 100%;

        @media screen and (min-width: 900px) {
          width: 70px;
          height: 195px;
          background-size: 100% 100%;
        }
      }
    }
    @media screen and (min-width: 600px) {
      min-height: 64px;
    }
    
    @media screen and (max-width: 1099px) {
      justify-content: space-between;
    }
  }
  
  &__inner {
    display: flex;
    align-items: center;

    .header__top-row & {
      justify-content: space-between;
    }

  }

  &__logo-img {
    margin-right: 10px;

    @media screen and (min-width: 600px) {
      margin-right: 22px;
    }
  }

  &__work-time {
    @media screen and (max-width: 1099px) {
      @include vis-hid;
    }

    @media screen and (min-width: 1100px) {
      padding: to-em(5px 0px 5px 30px, 1.2rem);
      font-size: 1.2rem;
      line-height: to-em(14px, 1.2rem);
      font-family: $ff--sec;
      background: url('../img/design/sprite.svg#clock1') no-repeat 0 center / 20px 20px; 
    }

  }

  &__main-menu-wrap {
    @media screen and (min-width: 1100px) {
      flex: 1 1 auto;
    }
  }
}
