.projects {
  padding-top: 40px;
  background-color: $c--darken;
  
  @media screen and (min-width: 900px) {
    padding-top: 76px;
  }

  &__header {
    margin-bottom: 23px;
    display: flex;
    color: $txt-c--sec;

    
    @media screen and (min-width: 600px) {
      margin-bottom: 35px;
      justify-content: space-between;
      align-items: baseline;
    }
  }

  &__title {
    margin-bottom: 0;
  }
  &__gallery {
    color: $txt-c--sec;
  }
  &__feedback {
    background-image: linear-gradient(180deg, $c--darken 75%, #fff 25%);
  }

  &__team {
    padding-top: 55px;
    background-color: $c--white;

    @media screen and (min-width: 600px) {
      padding-top: 105px;
    }
  }
}