.gallery {
  margin-bottom: 35px;
  
  @media screen and (min-width: 600px) {
    margin: 0 -15px;
    display: flex;
    flex-flow: row wrap;
  }

  &__item {
    position: relative;

    @media screen and (min-width: 600px) {
      padding: 0 15px;
      width: 50%;
    }

    &:not(:last-child) {
      margin-bottom: 36px;
      
      @media screen and (min-width: 600px) {
        margin-bottom: 52px;
      }
    }
  }

  &__tag {
    position: absolute;
    top: 10px;
    left: 10px;
    z-index: 1;
    
    @media screen and (min-width: 600px) {
      left: 35px;// if min-width: 600px i add margin-left/right -15px 
      top: 20px;
    }
  }

  &__desc {
    margin-bottom: 0;
    font-family: $ff--sec;

    @media screen and (max-width: 599px) {
      font-size: 1.3rem;
      line-height: to-em(19px, 1.3rem);
    }
  }
}