.accordions {

  &__title {
    margin-bottom: 0.9em;

    @media screen and (min-width: 900px) {
      margin-bottom: 1.05em;
    }
  }
  &__list {
    border-top: 1px solid $c--grey-light;
  }
}

.accordion {
  font-family: $ff--sec;
  border-bottom: 1px solid $c--grey-light;

  &__panel {
    position: relative;
    padding: to-em(28.5px 40px 28.5px 0px, 1.8rem);    
    width: 100%;
    border: none;
    font-family: inherit;
    font-size: 1.3rem;
    line-height: to-em(19px, 1.3rem);
    text-align: left;
    background-color: transparent;
    cursor: pointer;
    transition: color .2s ease-in-out;

    @media screen and (min-width: 600px) {
      padding-right: 55px;
      font-size: 1.8rem;
      line-height: to-em(23px, 1.3rem);
    }

    &:focus {
      outline: none;
    }

    .accordion.is-open &,
    &:hover {
      color: $txt-c--colored-prim;
    }
    .accordion.is-open &::after,
    &:hover::after {
      background-color: $c--prim-light;
      transition: background-color .2s ease-in-out,
                  transform .4s ease-in-out;
    }

    &::after {
      content: '\002B';
      position: absolute;
      top: 50%;
      right: 0;
      display: flex;
      justify-content: center;
      align-items: center;
      width: 29px;
      height: 29px;
      border-radius: 50%;
      border: 1px solid;
      font-size: 2rem;
      color: $c--prim;
      transform: translateY(-50%);
      background-color: transparent;

      @media screen and (min-width: 600px) {
        width: 44px;
        height: 44px;
        right: 14px;
      }
    }
    .accordion.is-open &::after {
      transform: translateY(-50%) rotate(135deg);
    }

  }
  &__drop {
    overflow: hidden;
    max-height: 0;
    transition: max-height .5s ease;
    
    .accordion.is-open & {
      max-height: 2000px;
    }
  }

  &__txt {
    position: relative;
    padding-right: 20px;
    margin-bottom: 30px;
    margin-right: 14px;
    border-right: 1px solid $c--prim;
    font-size: 1.3rem;
    line-height: to-em(19px, 1.3rem);
    
    @media screen and (min-width: 600px) {
      padding-bottom: 25px;
      font-size: 1.5rem;
      line-height: to-em(22px, 1.5rem);
      margin-right: 36px;
    }

    &::before, &::after {
      content: '';
      position: absolute;
      right: -3px;
      display: block;
      width: 5px;
      height: 5px;
      border-radius: 50%;
      background-color: $c--prim;
    }
    &::after {
      bottom: 0;
    }

  }
}