.filter {
   margin-right: -20px;
   
   @media screen and (min-width: 600px) {
     margin-right: 0;
     max-width: 420px;
   }
  &__btn {
    padding: to-em(3px 0px, 1.6rem);
    border: none;
    font-size: 1.6rem;
    line-height: to-em(17px, 1.6rem);
    font-family: $ff--prim;
    background-color: transparent;
    cursor: pointer;
    transition: color .2s ease-out,
                border-bottom-color .2s ease-out;
    white-space: nowrap;
    
    @media screen and (min-width: 600px) {
      padding: to-em(19px 0px 22px, 1.6rem);
      font-size: 1.8rem;
      border-bottom: 2px solid transparent;
    }

    &.is-active,
    &:hover {
      color: $txt-c--colored-prim;
      border-bottom-color: $c--prim;
    }
  }
}