.blog {
  &__title {
    margin-bottom: 15px;
    padding-bottom: 15px; 
    background: linear-gradient(to right, rgba(0, 0, 0, .8), rgba(0, 0, 0, .8)),
                url('../../img/design/blog_title_bg.jpg') no-repeat 50% 50% / cover;
    color: $c--white;
    
    @media screen and (min-width: 600px) {
      margin-bottom: 0;
    }
  }

  &__content {
    border-top: 1px solid rgba(0, 0, 0, .2);
    overflow: hidden;
      
    @media screen and (min-width: 600px) {
      border-bottom: 1px solid rgba(0, 0, 0, .2);
    }
  }

  &__content-inner {
    display: flex;
    flex-wrap: wrap;
  }

  &__preview,
  &__article {
    width: 100%;
  }

  &__preview {
    padding-bottom: 50px;

    @media screen and (min-width: 600px) {
      width: calc(50% + 15px);
    }
  }

  &__preview-list {
    margin-bottom: 50px;
  }

  &__preview-btn {
    padding: to-em(14.5px 15px, 1.3rem);
    display: none;
    width: 100%;
    border: none;
    font-family: $ff--sec;
    color: $txt-c--colored-prim;
    background-color: $c--prim-light;
    text-align: center;
    cursor: pointer;

    @media screen and (min-width: 600px) {
      width: calc(100% - 20px);
    }
    @media screen and (min-width: 900px) {
      padding: to-em(22px 15px, 1.5rem);
      width: calc(100% - 30px);
      font-size: 1.5rem;
    }
  }

  &__article {
    @media screen and (min-width: 600px) {
      width: calc(50% - 15px);
    }
  }
}

.article {
  position: relative;
  padding: 20px 0 30px 0;
  
  @media screen and (min-width: 600px) {
    padding-left: 20px;
    padding-bottom: 50px;

  }
  
  @media screen and (min-width: 900px) {
    padding-top: 38px;
    padding-left: 30px;
  }

  @media screen and (min-width: 1200px) {
    padding-left: 70px;
  }

  &:before {
    @media screen and (min-width: 600px) {
      content: '';
      position: absolute;
      z-index: -1;
      top: 0;
      left: 0;
      height: 100%;
      width: 100vw;
      background-color: $c--grey-light;
    }
   
  }
  &__content {
    margin-bottom: 44px;
  }
  &__title {
    margin-bottom: to-em(28px, 2.2rem);
  }

  &__txt {
    font-family: $ff--sec;
    font-size: 1.3rem;
    line-height: to-em(21px, 1.3rem);
    
    @media screen and (min-width: 900px) {
      font-size: inherit;
    }
    p {
      margin-bottom: to-em(18px, 1.5rem);
    }

    &:not(:last-child) {
      margin-bottom: 35px;
    }
  }

  &__gallery {
    margin-bottom: 30px;
  }
    
  &__video {
    padding-top: percentage(2/3);
    background-color: #000;
  }

  &__footer {
    display: flex;
    align-items: center;
  }
  &__footer-share {
    margin-right: 20px;
    font-family: $ff--sec;
    font-size: 1.2rem;
    line-height: to-em(22px, 1.2rem);
    color: $txt-c--colored-prim;
  }
  &__social-list {
    @extend %list-reset;

    display: flex;
  }
  &__social-item {

    &:not(:last-child) {
      margin-right: 10px;
    }
  }
  &__social-link {
    display: block;
    width: 28px;
    height: 28px;
    border: 1px solid rgba(0, 0, 0, .1);
    border-radius: 50%;
    text-decoration: none;
    background-repeat: no-repeat;
    background-position: center;
    transition: background-color .2s ease-out,
                border-color .2s ease-out;

    &:hover {
      background-color: $c--prim-light;
      border-color: $c--prim;
    } 
    &--fb {
      background-image: url('../../img/design/sprite.svg#fb');
      background-size: 6px 13px;
    }
    &--vk {
      background-image: url('../../img/design/sprite.svg#vk');
      background-size: 14px 8px;
    }
    &--ok {
      background-image: url('../../img/design/sprite.svg#ok');
      background-size: 8px 13px;
    }
  }
}

