.video {
  position: relative;

  video {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }

  &__btn {
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    top: 50%;
    left: 50%;
    padding: 0;
    width: 30px;
    height: 30px;
    border: none;
    border-radius: 50%;
    font-size: 1.2rem;
    background-color: transparent;
    transform: translate(-50%, -50%);
    cursor: pointer;
    
    @media screen and (min-width: 600px) {
      width: 40px;
      height: 40px;
    }
    &:hover::after,
    &:hover::before {
      background-color: $c--sec;
    }
    &::after, &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      z-index: -1;
      display: block;
      width: 100%;
      height: 100%;
      border-radius: 50%;
      background-color: $c--prim;
      transition: background-color .2s ease;
    }

    &::before {
      animation: pulse 2s ease-out infinite;
    }

  
  }
}

