.logo {
  display: flex;
  align-items: center;
  text-decoration: none;

  &__img {
    margin-bottom: -5px;
    width: 69px;

    @media screen and (min-width: 600px) {
      width: 94px;
    }
  }

  &__txt {
    max-width: 141px;
    font-family: $ff--sec;
    font-size: .8rem;
    line-height: to-em(12px, 0.8rem);

    @media screen and (min-width: 900px) {
      font-size: .9rem;
      line-height: to-em(13px, .9rem)
    }
  }
}
