.calc {

  &__title {
    margin-bottom: to-em(23px, 2.4rem);

    @media screen and (min-width: 900px) {
      margin-bottom: to-em(32px, 3rem);
    }
  }
}

.calc-form {

  @media screen and (min-width: 600px) {
    flex-flow: row wrap;
  }

  &__input, &__select {
    flex: 0 0 50%;
    
    @media screen and (min-width: 600px) {
      flex: 1 1 33.333%;
    }

    &:not(:first-of-type) {
      @media screen and (min-width: 600px) {
        border-left-color: transparent;
      }
    }
  }

  &__policy {
    @media screen and (min-width: 900px) {
      max-width: 450px;
    }
  }
}
