.manager {
    overflow: hidden;
  display: flex;
  flex-flow: column;
  align-items: center;
  max-width: 125px;
  text-decoration: none;

  &__pic {
    margin-bottom: 18px;
    width: 72px;
    border-radius: 50%;

    img {
      display: block;
      max-width: 100%;
      height: auto;
    }
  }
  &__name {
    max-width: 100%;
    span {
      display: block;
      width: 100%;
      @include txt-nowrap;
    }
    margin-bottom: 0;
    padding: 0 .5em;
    font-size: 1.6rem;
    line-height: to-em(18px, 1.6rem);
    font-weight: 600;
    text-align: center;
  }
}
