.input {
  padding: 0 20px;
  display: flex;
  align-items: center;
  min-width: 150px;
  min-height: 50px;
  border-top: none;
  border-bottom: none;
  border-left: 1px solid;
  border-right: 1px solid;
  font-family: $ff--sec;
  background-color: transparent;
  transition: background-color .2s ease-in-out,
              color .2s ease-in-out;

  @media screen and (max-width: 899px) {
    font-size: 1.3rem;
    line-height: to-em(18px, 1.3rem);
  }

  &--light {
    color: $txt-c--sec;
  }

  &--colored {
    color: $txt-c--colored-prim;
  }
  
  &::placeholder {
    color: inherit;
  }

  &:focus {
    outline: none;
    background-color: $c--prim-light;
    color: $txt-c--colored-prim;
  }
}